import * as React from 'react';
import Button from '@mui/material/Button';
import TextField from '@mui/material/TextField';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogTitle from '@mui/material/DialogTitle';
import { postData } from '../configs/request';

export default function FormDialog({ open, handleClose, toggleRerenderRoomList }) {
  const [name, setName] = React.useState("");
  const [description, setDescription] = React.useState("");

  const handleAddClass = async () => {
    try {
      const classItem = {
        name,
        description,
      }

      const response = await postData(`${process.env.REACT_APP_BASE_URL}/classes`, classItem);
      if (response?.isSuccess) {
        toggleRerenderRoomList();
      }
    } catch (err) {
      console.error(err);
    }
  }

  return (
    <div>
      <Dialog open={open} onClose={handleClose}>
        <DialogTitle>Táº¡o lá»p há»c</DialogTitle>
        <form
          onSubmit={(e) => {
            e.preventDefault();
            handleAddClass();
          }}
        >
          <DialogContent>
            <TextField
              autoFocus
              margin="dense"
              id="name"
              label="TÃªn lá»p há»c"
              required
              fullWidth
              variant="standard"
              onChange={(e) => {
                setName(e.target.value);
              }}
            />
            <TextField
              margin="dense"
              id="description"
              label="MÃ´ táº£"
              required
              fullWidth
              variant="standard"
              onChange={(e) => {
                setDescription(e.target.value);
              }}
            />
            {/* <TextField
              margin="dense"
              id="room"
              label="PhÃ²ng"
              required
              fullWidth
              variant="standard"
              onChange={(e) => {
                setRoom(e.target.value);
              }}
            /> */}
          </DialogContent>
          <DialogActions>
            <Button onClick={handleClose}>Huá»·</Button>
            <Button type="submit" onClick={handleClose}>Táº¡o</Button>
          </DialogActions>
        </form>
      </Dialog>
    </div>
  );
}
